import './FabricantePerfil.css'

import { IonAlert, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { AiFillStar } from 'react-icons/ai'
import { RouteComponentProps } from 'react-router'

import {
  AdminItemFieldRegistro,
  DashboardModalComponent,
  FooterFormulario,
  Page,
  SelectorModalRegistro,
} from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE } from '../../constants'
import { comprimirImagen, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Fabricante } from '../../services/api'

const previewDefault = {
  imagen_perfil: '',
}

export const FabricantePerfil: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Alert, setAlert] = useState(false)
  const [Loading, setLoading] = useState(false)
  const [Editando, setEditando] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const [Errors, setErrors] = useState<Fabricante | null>(null)
  const [Datos, setDatos] = useState<Fabricante>({} as any)
  const [Previews, setPreviews] = useState(previewDefault)
  const [IdInput, setIdInput] = useState('a')
  const DashaboardRef = useRef<any>()
  const addToast = useToaster()
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    if (loginStore.fabricante) {
      obtenerFabricante(loginStore.fabricante.id)
    }
  }, [])

  const obtenerFabricante = (idFabricante: number) => {
    rootStore.environment.api.adminGetFabricante(idFabricante).then((result) => {
      if (result.kind === 'ok') {
        loginStore.setFabricante(result.fabricante)
        setDatos(result.fabricante)
      }
    })
  }

  const alertAccion = () => {
    setAlert(true)
    setTitleAlert('Editar perfil')
    setTextAlert('Al editar su perfil este quedará pendiente de aprobación, confirma que desea editar?')
  }

  const alertEjecutarAccion = () => {
    guardar()
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos as any as Fabricante)
  }

  const setPreview = (campo: string, valor: any) => {
    let previews = { ...Previews }
    // @ts-ignore
    previews[campo] = valor
    setPreviews(previews)
  }

  const dataSelectorModalRubros = (data: any) => {
    setTimeout(() => setDato('rubros', data), 500)
  }

  const file = async (data: any) => {
    const imagen_comprimida = await comprimirImagen(data, IdInput, compromiendoImagen)
    // @ts-ignore
    setDato(IdInput, imagen_comprimida)
  }

  const compromiendoImagen = (porcentaje: number) => {
    if (porcentaje === 100) {
      setLoading(false)
    } else {
      setLoading(true)
    }
  }

  const filePreview = (data: any) => {
    console.log('file preview ==> ', data)
    setPreview(IdInput, data)
    DashaboardRef.current.toggle()
  }

  const seleccionarInput = (id: string) => {
    setIdInput(id)
    DashaboardRef.current.toggle()
  }

  const guardar = () => {
    setLoading(true)
    setErrors(null)
    if (Datos) {
      let datos = { ...Datos } as any
      // Quitar campos que no se actualizan en este form
      delete datos.constancia_ruc
      delete datos.dias_apertura
      delete datos.hora_apertura
      delete datos.hora_cierre
      delete datos.puntos_retiro
      delete datos.usuarios
      //
      rootStore.environment.api.guardarFabricante(datos).then((result: any) => {
        setLoading(false)
        if (result.kind === 'ok') {
          addToast('Perfil actualizado exitosamente', 'success')
          setEditando(false)
          history.push(RUTAS_APP_FABRICANTE.PERFIL, { guardado: true })
        } else {
          if (result.kind === 'bad-data') {
            addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
            setErrors(result.errors)
            if (result.errors.non_field_errors) {
              addToast(result.errors.non_field_errors[0], 'warning')
            }
          } else {
            addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
          }
        }
      })
    }
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const resenhaLength = Datos.resena_empresa ? Datos.resena_empresa.length : 0

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'perfil-fabricante'}>
      <SubHeader noContainer links={[{ titulo: 'Perfil', ruta: RUTAS_APP_FABRICANTE.PERFIL }]} />
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TitleAlert}
        message={TextAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => alertEjecutarAccion(),
          },
        ]}
      />
      <DashboardModalComponent
        ref={DashaboardRef}
        id_input={IdInput}
        file={file}
        file_preview={filePreview}
        facingMode={'environment'}
      />
      <div className="d-flex flex-column justify-content-end pt-3">
        <div className="d-flex align-items-center mb-3" style={{ gap: '1rem' }}>
          <div className={'imagen-perfil-container d-flex ml-4'}>
            {Previews['imagen_perfil'] !== '' && (
              <div>
                <img
                  className={'imagen-perfil imagen-perfil-editar-container  mx-auto'}
                  src={Previews['imagen_perfil']}
                  style={{ maxHeight: 200, maxWidth: 200 }}
                  alt={'Imagen de perfil'}
                />
              </div>
            )}
            {Previews['imagen_perfil'] === '' && Datos && (
              <div style={{position:'relative'}}>
                <img
                  className={'imagen-perfil imagen-perfil-editar-container  mx-auto'}
                  src={Datos.imagen_perfil}
                  style={{ maxHeight: 200, maxWidth: 200 }}
                  alt={'Imagen de perfil'}
                />
                <div
                  className="icono-camara-imagen-perfil-fabricante"
                  onClick={() => {
                    if (!Editando) {
                      return
                    }
                    seleccionarInput('imagen_perfil')
                  }}
                >
                  <FIcon.Camera />
                </div>
              </div>
            )}
          </div>
          <div className="nombre-fabricante-container">
            <span className='nombre'>{Datos.nombre}</span>
            <br />
            <span>Id#{Datos.id}</span>
            <div className="rating">
              {' '}
              <strong> {Datos.rating} </strong> <AiFillStar />
            </div>
          </div>
        </div>
        <div>
          <div className="px-3">
            <div className={'row'}>
              <div className={''}>
                {Errors && Errors.imagen_perfil && <div className={'text-danger'}>{Errors.imagen_perfil}</div>}
              </div>
            </div>
            <div className="row">
              <AdminItemFieldRegistro
                titulo={'RUC'}
                titulo_externo={'RUC'}
                tipo={'texto'}
                campo={'ruc'}
                valor={Datos ? Datos.ruc : ''}
                error={Errors && Errors.ruc}
                set_dato={setDatoInput}
                columnas_sm={6}
                columnas={12}
                deshabilitado={Editando}
              />

              <AdminItemFieldRegistro
                titulo={'Razón social'}
                titulo_externo={'Razón social'}
                tipo={'texto'}
                campo={'razon_social'}
                valor={Datos ? Datos.razon_social : ''}
                error={Errors && Errors.razon_social}
                set_dato={setDatoInput}
                columnas_sm={6}
                columnas={12}
                deshabilitado={Editando}
              />
            </div>

            <div className="row">
              <AdminItemFieldRegistro
                titulo={'Teléfono'}
                titulo_externo={'Teléfono'}
                tipo={'texto'}
                campo={'nro_telefono'}
                valor={Datos ? Datos.nro_telefono : ''}
                error={Errors && Errors.nro_telefono}
                set_dato={setDatoInput}
                columnas_sm={6}
                columnas={12}
                deshabilitado={Editando}
              />
              <AdminItemFieldRegistro
                titulo={'Celular'}
                titulo_externo={'Celular'}
                tipo={'telefono-simple'}
                campo={'celular'}
                valor={Datos.celular ? Datos.celular : ''}
                error={Errors && Errors.celular}
                set_dato={setDatoInput}
                columnas_sm={6}
                columnas={12}
                deshabilitado={Editando}
              />
              <AdminItemFieldRegistro
                titulo={'Correo'}
                titulo_externo={'Correo'}
                tipo={'email'}
                campo={'email'}
                valor={Datos.email ? Datos.email : ''}
                error={Errors && Errors.email}
                set_dato={setDatoInput}
                columnas={12}
                deshabilitado={Editando}
              />
              <AdminItemFieldRegistro
                titulo={'Página web'}
                titulo_externo={'Página web'}
                tipo={'texto'}
                campo={'sitio_web'}
                valor={Datos.sitio_web ? Datos.sitio_web : ''}
                error={Errors && Errors.sitio_web}
                set_dato={setDatoInput}
                columnas={12}
                deshabilitado={Editando}
              />
              <div className={'col-12 col-lg-6'}>
                <SelectorModalRegistro
                  titulo={'Rubros'}
                  titulo_label={'Rubros'}
                  listado_elementos={rootStore.rubros}
                  listado_elementos_seleccionados={Datos ? Datos.rubros : []}
                  datos_filtrados={dataSelectorModalRubros}
                  multiple={true}
                  llave_label_opcion={'nombre'}
                  deshabilitado={Editando}
                />
                {Errors && Errors.rubros && <div className={'text-danger'}>{Errors.rubros}</div>}
              </div>
              <AdminItemFieldRegistro
                  titulo={'Horario de atención'}
                  titulo_externo={'Horario de atención'}
                  tipo={'texto'}
                  campo={'horario_atencion'}
                  valor={Datos.horario_atencion ? Datos.horario_atencion : Datos.horario_dias_atencion}
                  error={Errors && Errors.horario_atencion}
                  set_dato={setDatoInput}
                  columnas={12}
                  deshabilitado={Editando}
              />
            </div>

            <div className='row'>
              <AdminItemFieldRegistro
                titulo={'Reseña de la empresa'}
                titulo_externo='Reseña de la empresa'
                tipo={'textarea'}
                campo={'resena_empresa'}
                valor={Datos.resena_empresa}
                error={Errors && Errors.resena_empresa}
                set_dato={setDatoInput}
                columnas={12}
                textAreaRows={3}
                help_text={`${resenhaLength}/200`}
                deshabilitado={Editando}
              />
            </div>
          </div>
        </div>
       <FooterFormulario className="py-4 d-flex flex-row">
          {!Editando && <button className={'btn btn-outline-primary px-lg-5 mt-4 mr-3'} onClick={() => setEditando(true)}>
            Editar perfil
          </button>}
          <button className={'btn btn-outline-primary px-lg-4 mt-4'} onClick={() => history.push(RUTAS_APP_FABRICANTE.PUNTOS_RETIRO)}>
            Puntos de retiro
          </button>
          {Editando && <button className={'btn btn-primary px-lg-5 mt-4 ml-3'} onClick={() => alertAccion()}>
            {Loading && <IonSpinner name="crescent" />}
            {!Loading && <span>Guardar</span>}
          </button>}
       </FooterFormulario>
      </div>
    </Page>
  )
})

export default FabricantePerfil
